@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin banner-dimensions {
  width: 100%;
  height: 111px;
  border-radius: 10px;

  @media screen and (min-width: $sm) {
    height: 223px;
    border-radius: 25px;
  }
}

@mixin banner-template(
  $imgUrl,
  $height: 223px,
  $border-radius: 25px,
  $mobile-height: 111px,
  $mobile-border-radius: 10px
) {
  @include banner-dimensions;
  background-image: url($imgUrl);
  background-size: cover;
}

@mixin banner-opacity($opacity) {
  background: rgba(0, 0, 0, $opacity);
}
